	  'Group': '',
	  'Sort': '86',
	  'Description': 'MarketDataSnapshotFullRefresh',
	  'Elaboration': 'The Market Data messages are used as the response to a Market Data Request message. In all cases, one Market Data message refers only to one Market Data Request. It can be used to transmit a 2-sided book of orders or list of quotes, a list of trades, index values, opening, closing, settlement, high, low, or VWAP prices, the trade volume or open interest for a security, or any combination of these.'
