	  'Sort': '90',
	  'Description': 'QuoteStatusRequest',
	  'Elaboration': 'The quote status request message is used for the following purposes in markets that employ tradeable or restricted tradeable quotes:\n• For the issuer of a quote in a market to query the status of that quote (using the QuoteID to specify the target quote).\n• To subscribe and unsubscribe for Quote Status Report messages for one or more securities.'
