	  'Value': 'i',
	  'SymbolicName': 'MassQuote',
	  'Group': '',
	  'Sort': '98',
	  'Description': 'MassQuote',
	  'Elaboration': 'The Mass Quote message can contain quotes for multiple securities to support applications that allow for the mass quoting of an option series. Two levels of repeating groups have been provided to minimize the amount of data required to submit a set of quotes for a class of options (e.g. all option series for IBM).'
